import { CardActionArea, CardMedia, Grid, Link } from "@material-ui/core";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import { makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import React from "react";

const useStyles = makeStyles({
	root: {
		background: "none",
		display: "flex",
		height: "150px",
		width: "250px",
		alignItems: "center",
	},
	media: {
		padding: 10,
		maxWidth: "100px",
	},
});
export const NextGoals: React.FC = () => {
	const classes = useStyles();

	return (
		<>
			<Grid direction="row" container justifyContent="center">
				<Typography style={{ textAlign: "center", width: "100%", marginTop: "20px" }}>My next goals.</Typography>
				<Card style={{ background: "none" }}>
					<Link underline="none" href="https://reactjs.org/">
						<CardActionArea className={classes.root}>
							<CardContent>
								<Typography color="primary" gutterBottom>
									Senior React Developer
								</Typography>
							</CardContent>
							<CardMedia
								className={classes.media}
								component="img"
								image="data:image/svg+xml;base64,"
								title="nest"
							/>
						</CardActionArea>
					</Link>
				</Card>
				<Card style={{ background: "none" }}>
					<Link underline="none" href="https://nestjs.com/">
						<CardActionArea className={classes.root}>
							<CardContent>
								<Typography color="primary" gutterBottom>
									Senior Nest Developer
								</Typography>
							</CardContent>
							<CardMedia className={classes.media} component="img" image="https://d33wubrfki0l68.cloudfront.net/e937e774cbbe23635999615ad5d7732decad182a/26072/logo-small.ede75a6b.svg" title="react" />
						</CardActionArea>
					</Link>
				</Card>
			</Grid>
		</>
	);
};
