import { Avatar, Box, Grid, Typography } from "@material-ui/core";
import React from "react";
import faceAvatar from "../../../assets/img/krasnansky.png";

export const About: React.FC = () => {
  return (
    <>
      <Box>
        <Avatar
          alt="Michal Krasnansky"
          src={faceAvatar}
          style={{ marginBottom: "10px", height: "150px", width: "150px" }}
          sizes="xl"
        />
        <Typography variant="body1" color="secondary" component="p">
          Ahoj v oblasti webu a aplikci sa pohybujem u skoro dva roky.
          Spoiatku som hadal cestu, ako sa niekam dosta v tomto obore.
          Vytudoval som umeleck kolu, ktor bola nabit kreativitou po cel
          as, o som tam chodil. Mj zujem ani po rokoch prce v inch
          odvetviach neopadol. Rd by som zitkoval vetko, o som sa nauil a
          pretavil to do vne, ktor mm k technolgim. Dnes som s
          programovanm vo fze, kde u viem zpasi aj so zloitejmi
          procesmi.
        </Typography>
        <Typography variant="body2" color="primary" component="p">
          Mj hlavn bod zujmu je React a z backendu Nest. Nie je to tak dvno,
          o ma tieto technolgie oslovili, ale vetko, o som sa nauil viem
          rozvja alej za pomoci tchto dvoch silnch nstrojov. Kadm dom
          sa dostvam alej a som van za kadho, kto mi s tm pomohol, i u
          bvali kolegovia z teamu alebo autori na Stack Overflow.
        </Typography>
        <Grid container direction="row-reverse">
          <Typography>akujem Michal!</Typography>
        </Grid>
      </Box>
    </>
  );
};
